import React from "react";

// When I write upper & lower it means higher or lower point of a rectangle & left, right is self-expantory.

// Starting from upper right side. to lower left side.
const UpperRightFiveDegreeCut = () => (
  <div className="absolute bottom-0 triangle-height">
    <div className="cut border-left-white" />
  </div>
);

// Starting from bottom left side to upper right side.
export const BottomLeftFiveDegreeCut = () => (
  <div className="triangle-height">
    <div className="cut switch-cut-width border-top-white border-right-offWhite" />
  </div>
);

// Starting from the upper left side to lower right side.
export const UpperLeftFiveDegreeCut = () => (
  <div className="triangle-height">
    <div className="cut border-top-offWhite border-left-white" />
  </div>
);

// Starting from the lower left side to upper right
export const FooterLeftFiveDegreeCut = () => (
  <div className="triangle-height">
    <div className="cut switch-cut-width border-top-white border-right-offBlack" />
  </div>
);
// Starting from lower right to upper left.
export const FooterRightFiveDegreeCut = () => (
  <div className="triangle-height">
    <div className="cut border-top-offWhite border-left-offBlack" />
  </div>
);

export default UpperRightFiveDegreeCut;
