import React from "react";
import PropTypes from "prop-types";

export const Title = ({ children }) => (
  <h1 className="typography-h1-title">{children}</h1>
);

Title.propTypes = {
  children: PropTypes.node.isRequired,
};

export const Subtitle = ({ children }) => (
  <p className="typography-subtitle">{children}</p>
);

Subtitle.propTypes = {
  children: PropTypes.node.isRequired,
};

export const Header = ({ tag, children }) => {
  let component;
  switch (tag) {
    case "h2":
      component = (
        <h2 className="typography-h2 pb-8 sm:pb-16">
          <b className="text-primary">{"// "}</b>
          {children}
        </h2>
      );
      break;
    case "h2-alt":
      component = (
        <h2 className="typography-h2 pb-4 sm:pb-4">
          <b className="text-primary">{"// "}</b>
          {children}
        </h2>
      );
      break;
    case "h2-alt2":
      component = (
        <h2 className="typography-h2">
          <b className="text-primary">{"// "}</b>
          {children}
        </h2>
      );
      break;
    case "h3":
      component = <h3 className="typography-h3">{children}</h3>;
      break;
    case "h4":
      component = <h4 className="typography-h4">{children}</h4>;
      break;
    default:
      // Default to h4.
      component = <h4 className="typography-h4">{children}</h4>;
      break;
  }
  return component;
};

Header.propTypes = {
  tag: PropTypes.oneOf(["h2", "h3", "h4"]).isRequired,
  children: PropTypes.node.isRequired,
};

export const Body = ({ children }) => (
  <p className="typography-body mt-4">{children}</p>
);

Body.propTypes = {
  children: PropTypes.node.isRequired,
};

export const NoStylesBody = ({ children }) => (
  <p className="typography-body">{children}</p>
);
NoStylesBody.propTypes = {
  children: PropTypes.node.isRequired,
};

export const WhiteNoStylesBody = ({ children }) => (
  <p className="typography-body typography-white">{children}</p>
);
WhiteNoStylesBody.propTypes = {
  children: PropTypes.node.isRequired,
};
